.admin-activity-detail {
  max-width: 900px;
  margin: auto;
  border: 1px solid lightgrey;
  padding: 20px;
  border-radius: 15px;
  box-shadow: 0 0 7px lightgrey;
  background-color: #ddd;

  p {
      width: 100%;
  }

  h2 {
    color: $clr1;
    text-align: center;
    margin-bottom: 30px;;
  }

  span {
      font-weight: 700;
      text-decoration: underline;
      color: $clr1;
  }

  .player {
      text-align: center;
  }
}
