.week-full-container {
    margin: 10px 0;
    display: flex;
    align-items: center;

    .week-container {
        display: grid;
        grid-template-columns: repeat(7, 1fr);
        // padding: 10px;
        justify-items: center;
        column-gap: 5px;

        div:first-child {
            background-color: #3D3D3D;
            color: white;
        }

    }
}
