.activity-form {
  .form-header {
    text-align: center;
  }

  .activity-form-container {
    textarea,
    input[type='text'],
    input[type='number'] {
      width: 100%;
      padding: 5px;
      //   outline: none;
      border: none;
    }

    input[type='radio'] {
      margin-inline-start: 15px;
      margin-inline-end: 3px;
      
    }
    input[type='checkbox'] {
      margin-inline-start: 15px;
      margin-inline-end: 3px;
    }
    .form-group {
      margin-bottom: 10px;
    }

    padding: 30px;
    width: fit-content;
    margin: 20px auto;
    background-color: #ddd;
    border-radius: 20px;
  }
}
