.admin-header {
  background-color: $clr5;
  padding: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0 0 3px black;
  margin-bottom: 30px;
  

  nav {
    // background-color: $clr4;
    padding: 5px 0;
    
    & * {
      margin-inline-end: 20px;
      text-decoration: none;
      padding: 5px 20px;
      background-color: $mind-clr4;
      border-radius: 10px;
      // transition: all 1.5s;
      

      &:focus,
      &:hover,
      &:visited,
      &:link,
      &:active {
        text-decoration: inherit;
        color: inherit;
      }
      
      &:hover {
        // background-color: darken($mind-clr4, 30%);
        background-color: $mind-clr3;
        transform: scale(1.5);
      }
    }
    .active {
      background-color: $mind-clr3;
      // background-color: black;
      // color: white;
    }
  }

  ul {
    list-style: none;

    // li:first-child{
    //   height: 20px;
    // }
    li {
      display: inline-block;
      margin-inline-end: 20px;
    }
  }
  // nav a:not(:first-child) {
  //     border-top: 1px solid $clr1;
  //     padding: 0 5px;
  //     margin: 10px;
  // }
  // nav .active {
  //   color: $clr1;
  // }
}
