.admin-activity-item {
  // display: flex;

  // & > * {
  //     padding-inline-end: 20px;
  // }
  td {
    padding: 1em;
    background: #ddd;
    border-bottom: 2px solid white;
    text-align: center;

    &:nth-child(1),
    &:nth-child(2) {
      color: unset;
    }
    &:nth-child(1):hover,
    &:nth-child(2):hover {
      color: $clr1;
    }
  }

  td:nth-child(1),
  td:nth-child(2) {
    cursor: pointer;
  }

  &:hover {
    background-color: rgb(255, 250, 250);

  }
}


